import axios from "axios";
const base1=window.location.origin
function m(){
    axios.post(base1+'/WeiXin_war/point',{name:'一个地点'})
        .then(res=>{
            console.log(res)
        })
}
const require=axios.create({
    //baseURL:window.location.href+'point'
   // baseURL:'http://localhost/WeiXin_war/point'
    baseURL:base1+'/WeiXin_war/point'
})
function get(){
    return require.get('/selectAllPoints')
}
function signal_search(data){
    return require.get('/selectPointByCondition',{
        params:data
    })
}
function add_loc(data){
    console.log('我发送了',data)
    return require.get('/addPoint',{
        params:data})
    //return require.post('/addPoint', data)
}
function update(data){
    return require.get('/updatePoint',{
        params:data
    })
    //return require.post('/updatePoint',data)
}

function delete_loc(item){
    return require.get('/deletePointByName?id='+item.id+'&name='+item.name)
}
export {m, get, signal_search, add_loc, update, delete_loc}