<template>
  <div class="container" v-show="isShow" ref="input" style="display: inline">
    <form @submit.prevent="signal_search('name')" >
      名字<input type="text" v-model="search_content.name">
      <button class="go" >&#xe986;</button>
   </form>
    <button id="showAll" @click="this.$emit('show_all')" style="display: inline">展示全部</button>
<!--    <form @submit="signal_search('type')">-->
<!--      类别<input type="text" v-model="search_content.type">-->
<!--      <button class="go" >&#xe986;</button>-->
<!--    </form>-->
  </div>
    <button @click="isShow=!isShow">{{showText}}</button>
</template>

<script>
import {signal_search} from "@/require/api";
export default {
  name: "Search_bar",
  computed:{
    showText(){
      if (this.isShow)return '隐藏'
      else return '显示'
    }
  },
  data(){
    return{
      isShow:true,
      search_content:{
        name:'',
        longitude:'',
        latitude:'',
        type:''
      }
    }
  },
  methods:{
    change_input_focus(up){
      const c=this.$refs.input.querySelectorAll('input')
      if (up)c[0].focus()
      else c[1].focus()
    },
    signal_search: function (content) {
      let a = this.search_content[content]
      console.log(a)
      const para={}
      para[content]=a
      signal_search(para)
          .then(res => {
            console.log(res)
            this.$emit('show_search_result', res.data)
          })
    }
  },
  emits:['show_search_result','show_all']
}
</script>

<style scoped>
#big_search_btn{
  width: 100px;
  text-align: center;
}
button{
  font-family: 'icomoon',serif ;;
}
.search{
  float: left;
}
.go{
  width: 40px;

}
</style>